@import url(https://fonts.googleapis.com/css2?family=Jost&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jost&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jost&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body {
	margin: 0;
	padding: 0;
	height: 100vh;
	background-color: #1D1D1D;
	font-family: 'Jost', sans-serif;
}

h1, h2, h3, h4 {
	margin: 0;
}

ul {
	margin: 0;
	padding: 0;
	list-style: none;
}

li {
	margin: 0;
	padding: 0;
}

.app-container {
	height: 100vh;
	display: grid;
	grid-template-columns: repeat(24, 1fr);
	grid-template-rows: repeat(24, 1fr);
	background-color: #222222;
	color: #999999;
}

.flex-center {
	display: flex;
	justify-content: center;
	align-items: center;
}

.hover-transition {
	transition: 0.2s;
}

.hover-transition:hover {
	background-color: #444444;
	transition: 0.2s ease-out;
	cursor: pointer;
}

.user-select {
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

.file-menu-bar {
	grid-column: 1 / 26;
	grid-row: 1 / 3;
	display: flex;
	flex-flow: column nowrap;
	/*justify-content: flex-start;
	align-items: center;*/
	background-color: #060714;
	text-transform: uppercase;
	/*font-family: 'Jost', sans-serif;*/
}

.top-file-menu-bar {
	width: 100%;
	height: 50%;
	font-size: 0.7em;
	border-bottom: 1px solid #262630;
}

.menu-container {
	/*margin: 0 1em 0 1em;*/
	width: 40%;
	height: 100%;
	display: flex;
	justify-content: space-around;
	align-items: center;
}

.file-menu {
	width: 20%;
	height: 100%;
	/*padding: 0 1em 0 1em;*/
}

/*.file-menu:hover {
	background-color: #444444;
}*/

.options-menu {
	width: 30%;
	height: 100%;
	/*padding: 0 0 0 1em;*/
}

.workspace-menu {
	width: 40%;
	height: 100%;
	/*padding: 0 0 0 1em;*/
}

.window-menu {
	width: 30%;
	height: 100%;
	/*padding: 0 0 0 1em;*/
}

.bottom-file-menu-bar {
	width: 100%;
	height: 50%;
	border-bottom: 1px solid #262630;
}

.icons {
	width: 10%;
	height: 100%;
	margin-left: 1em;
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-around;
	align-items: center;
}
.object-browser {
	grid-column: 1 / 6;
	grid-row: 3 / 25;
	display: flex;
	flex-flow: column nowrap;
	justify-content: space-between;
	background-color: #060714;
	/*transition: 1s;*/
}

.object-browser-header-container {
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-around;
	align-items: center;
	border-bottom: 1px solid #262630;
}

.object-browser-header {
	font-size: 1.2em;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	/*text-transform: italic;*/
}

.object-tab-container {
	/*height: 5%;*/
	padding: 1em;
	display: flex;
	flex-flow: column nowrap;
	justify-content: center;
	/*background-color: gold;*/
	/*border-bottom: 1px solid white;*/
	border-bottom: 1px solid #262630;
	/*transition: 1s;*/
}

.object-tab {
	/*height: 100%;*/
	display: flex;
	/*justify-content: flex-start;*/
	justify-content: space-between;
	align-items: center;
	/*border-bottom: 1px solid #999999;*/
	cursor: pointer;
	/*transition: 1s;*/
}

.object-tab-text {
	margin-left: 5%;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	transition: .2s ease-out;
}

.object-tab-chevron {
	transition: .2s ease-out;
}

.rotatedChevron {
	/*transform: rotate(180deg);*/
	transform: rotate(-180deg);
	transition: .2s ease-out;
}

.object-tab-children-container {
	/*display: flex;*/
	/*height: 10%;*/
	/*transition: 1s;*/
}

.object-tab-children-list {
	/*fix*/
	margin-left: 2em;
	/*transition: 1s;*/
}

.object-tab-child {
	/*transition: 1s;*/
}

.collapsed {
	/*display: none;*/
	height: 0;
	opacity: 0;
	/*transition: 1s;*/
}

.object-input-container {

	width: 100%;
}

.object-input-form {
	width: 100%;
	display: flex;
	flex-flow: row nowrap;
}

.object-input {
	width: 80%;
	/*margin-left: 1em;*/
	padding: .5em .5em .5em 1.5em;
	font-family: 'Jost', sans serif;
	font-size: 1em;
	color: white;
	background-color: #262630;
	border: none;
}

.object-input-btn {
	width: 20%;
	font-family: 'Jost', sans serif;
	color: white;
	background-color: #262638;
	border: none;
	border: 1px solid #555555;
}

.object-input-btn:active {
	background-color: #262633;
}

.hidden {
	display: none;
}
.object-browser-icon {
	position: relative;
	top: 1px;
	transition: .2s;
}

.object-browser-icon-container {
	/*height: 100%;*/
	position: relative;
}

.object-browser-icon:hover {
	color: white;
	cursor: pointer;
	transition: .2s ease-out;
}

.tooltip {
	/*width: 7em;*/
	/*height: 4em;*/
	position: absolute;
	top: 1.7em;
	left: -2.05em;
	padding: 0.5em;
	background-color: white;
	border-radius: 5px;
	box-shadow: 3px 6px 10px #000000;
	z-index: 10;
}

.tooltip-option {
	/*margin: 0 1em 0 1em;*/
	/*margin: 0 .5em 0 .5em;*/
	padding: 0 .5em 0 .5em;
	/*border-bottom: 1px solid #999999;*/
	cursor: pointer;
	transition: .1s ease-out;
}

.tooltip-option:hover {
	background-color: #333333;
	cursor: pointer;
	transition: .1s ease-out;
}

.hidden {
	display: none;
}
.script-editor-container {
	grid-column: 6 / 21;
	grid-row: 3 / 25;
	display: flex;
	flex-flow: column nowrap;
	/*justify-content: center;
	align-items: center;*/
	background-color: #060714;
	border-left: 1px solid #262630;
	border-right: 1px solid #262630;
}

.script-editor-tab-container {
	width: 100%;
	height: 10%;
	/*padding: 1em;*/
	display: flex;
	justify-content: flex-start;
	align-items: center;
	color: white;
}

.script-editor-tab-list {
	margin-left: 1em;
	width: 100%;
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-around;
	align-items: center;
	/*background-color: gold;*/
}

.script-editor-tab {
	width: 15%;
	height: 50%;
	padding: 0.7em;
	display: flex;
	flex-flow: row nowrap;
	justify-content: center;
	align-items: center;
	/*font-size: 0.7em;*/
	font-size: 0.8em;
	border: 1px solid white;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	cursor: pointer;
	transition: .2s ease-out;
	opacity: 0.3;
}

.script-editor-tab:hover {
	background-color: #222226;
	/*opacity: 10;*/
	transition: .2s ease-out;
}

.script-editor {
	/*position: relative;*/
}

.script-editor-action-btns {
	width: 100%;
	height: 5%;
	display: flex;
	flex-flow: row nowrap;
	justify-content: flex-start;
	align-items: center;
	/*background-color: gold;*/
}

.script-action-btn {
	margin-left: 1em;
	transition: .2s ease-out;
}

.script-action-btn:hover {
	color: white;
	cursor: pointer;
	transition: .2s ease-out;
}

.throttle {
	margin-left: 1em;
}

/*.play:hover {
	color: green;
}

.pause:hover {
	color: gold;
}

.stop:hover {
	color: #990000;
}*/

.active-tab {
	background-color: #333333;
	opacity: 0.5;
}
.script-element {
	padding: 0.5em 0 0.5em 0;
	display: flex;
	flex-flow: row nowrap;
	justify-content: center;
	align-items: center;
	border-bottom: 1px solid #262630;
	/*opacity*/
	transition: 0.2s;
}
.script-elements {
	grid-column: 21 / 25;
	grid-row: 3 / 25;
	background-color: #060714;
}

.script-elements-header-container {
	padding: 0.5em;
	display: flex;
	flex-flow: column nowrap;
	justify-content: center;
	align-items: center;
	border-bottom: solid;
}

.script-elements-header {
	/*text-align: center;*/
	font-size: 1em;
	text-transform: uppercase;
}
